// vmvuex
module.exports = (vm) => {
    // 
	//
	if(process.env.NODE_ENV==='development'){
		uni.$u.http.setConfig((config) => {
		    /* config */




			config.baseURL = 'http://localhost:5000'; /*  */

		    return config
		})
	//
	}else{
		uni.$u.http.setConfig((config) => {
		    /* config */




		    config.baseURL = 'http://8.142.168.153:5000'; /*  */

		    return config
		})
	}

	
	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
	    // dataundefined{}
	    config.data = config.data || {}
		// customtoken
		// if(config?.custom?.auth) {
		// 	// vmvuexvm.$store.state
		// 	config.header.token = vm.$store.state.userInfo.token
		// }
	    return config 
	}, config => { // async await  
	    return Promise.reject(config)
	})
	
	// 
	uni.$u.http.interceptors.response.use((response) => { /*  async await */
		const data = response.data
		// 
		const custom = response.config?.custom
		if (data.code !== 200) { 
			// customtoastfalsetoast
			if (custom.toast !== false) {
				uni.$u.toast(data.message)
			}

			// catchreject
			if (custom?.catch) {
				return Promise.reject(data)
			} else {
				// pendingpromisecatch
				return new Promise(() => { })
			}
		}
		console.log('return',data.result)
		return data.result === undefined ? {} : data.result
	}, (response) => { 
		//  statusCode !== 200
		return Promise.reject(response)
	})
}